/* Ripple magic */
.ripple-effect {
  position: relative;
  overflow: hidden; }
  .ripple-effect:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 5px;
    height: 5px;
    background: rgba(255, 255, 255, 0.5);
    opacity: 0;
    border-radius: 100%;
    transform: scale(1, 1) translate(-50%);
    transform-origin: 50% 50%; }
  .ripple-effect:hover:after {
    animation: ripple 1s ease-out; }

@keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.5; }
  20% {
    transform: scale(60, 60);
    opacity: 0.3; }
  100% {
    opacity: 0;
    transform: scale(100, 100); } }

@-webkit-keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.5; }
  20% {
    transform: scale(60, 60);
    opacity: 0.3; }
  100% {
    opacity: 0;
    transform: scale(100, 100); } }

